<template>
  <div id="removeDepartment">
    <v-row justify="center">
      <!-- <v-btn color="primary" dark @click.stop="aaa" class="ml-3 mr-4 mt-1 mb-1" wi>增加部门</v-btn> -->
      <v-icon title="删除部门" class="ml-6" v-text="icon" @click.stop="isSelectDept" />
      <v-dialog v-model="dialog" persistent max-width="300px">
        <v-card>
          <v-card-title>
            确定删除本部门吗?
            <v-spacer />
            <v-icon aria-label="Close" @click="dialog = false">mdi-close</v-icon>
          </v-card-title>

          <v-card-text class="pb-6 pt-12 text-center">
            <v-btn class="mr-3" text @click="dialog = false">取消</v-btn>

            <v-btn color="success" text @click="doRemove(parentUniqueCode)">确定</v-btn>
          </v-card-text>
        </v-card>
      </v-dialog>
    </v-row>
  </div>
</template>

<script>
export default {
  data: () => ({
    color: "orange",
    icon: "mdi-close-thick",

    lazy: false,
    valid: true,
    dialog: false,

    departmentObj: {
      departmentName: "",
      departmentCode: "",
      numIndex: "",
      description: "",
      id:""
    }
  }),
  props: {
    parentUniqueCode: {
      type: Object
    }
  },
  methods: {
    doRemove(code) {
      if (!code) {
        //初始化加载不回调
      } else {
        //1. 发请求删除部门
       
        let postData = {uniqueCode:code.uniqueCode};
        this.$http.post('/user/api/v1/department/deleteDepartment ', postData)
        .then(response => {
          if (response.success) {
            response.msg="部门删除成功";
            // //2. 回调向父组件传参
            this.$emit("delOrg",this.parentUniqueCode.uniqueCode);
          }
          this.$store.commit('SET_SNACK',response)
        })
        .catch(error =>{
          console.log(error);
        })

        
      }
      this.dialog = false;
    },

    isSelectDept() {
        console.log(this.parentUniqueCode)
        if (!this.parentUniqueCode) {
          this.dialog = false;
          this.$store.commit("SET_SNACK", {
            success: false,
            msg: "请先选择部门"
          });
        }else{
           this.dialog = true;
        }
    }
  }
};
</script>

